#About{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 40px;
}

.AboutTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.AboutDesc{
    font-weight: 500;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
    letter-spacing: 0.5px;
}

.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
    color: black;
}

.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: #FAFAFA;
}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
} 

.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 380;
    letter-spacing: 0.5px;
    margin: 0.5rem 0;
}

@media screen and (max-width:720px ) {
    .skillBarText>p{
        font-size: 2vw;
    }
}

@media screen and (max-width:480px ) {
    .skillBarText>p{
        font-size: 3vw;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBarText{
        font-size: 5vw; 
    }
}